import * as React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import ImgSharp from '~components/utils/ImgSharp/ImgSharp';

import styles from './Carousel.module.css';
//import * as Flickity from 'flickity';

import './flickity.css';

import { CarouselQuery } from './__generated__/CarouselQuery';
//import useIntlHelper from '~components/utils/Intl/IntlHelper';

import { LocaleContext } from '~context/Context/LocaleContext';

const CarouselCards = () => {
	const data: CarouselQuery = useStaticQuery(QUERY);

	const { activeLocale } = React.useContext(LocaleContext);
	React.useEffect(() => {
		//const flick = document && document.getElementById('flick');
		const Flickity = typeof window !== `undefined` ? require('flickity') : null;
		const flick = new Flickity('#flick', { autoPlay: 1500, wrapAround: true });
		return () => {
			flick.destroy();
		};
	}, []);
	if (typeof window !== `undefined`) {
		return (
			<div id={`carousel+${activeLocale}`} className="inner">
				<div className={styles.feed}>
					<div id="flick" dir="ltr">
						{data.carousels.nodes.map(photo => (
							<div
								key={photo.name}
								className={styles.sliderCard}
								id={photo.name}
							>
								<a className={styles.cardImgLink} href="#">
									{/* 							<div
									className={styles.cardImg}
									style={{
										backgroundImage: 'url(https://picsum.photos/300/200)',
										backgroundSize: '100% 100%',
									}}
								></div> */}
									<ImgSharp
										className={styles.cardImg}
										alt={photo.name}
										/* 	sizes="(max-width: 1000px) 400px, 700px" */
										fluid={photo.childImageSharp.fluid}
									/>
								</a>
							</div>
						))}
					</div>
				</div>
			</div>
		);
	} else {
		return <div>Loading</div>;
	}
};

export default CarouselCards;

const QUERY = graphql`
	query CarouselQuery {
		carousels: allFile(
			filter: { relativeDirectory: { eq: "_data/img/carousel" } }
		) {
			nodes {
				childImageSharp {
					fluid(maxWidth: 800) {
						...GatsbyImageSharpFluid_withWebp_tracedSVG
					}
				}
				name
			}
		}
	}
`;
