import * as React from 'react';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import { Index, Index_posts_edges_node } from '~pages/__generated__/Index';
import PostCard from '../PostCard/PostCard';

export const PostFeed: React.FC<{ data: Index }> = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<div className="posts">
			<h2>{formatMessage('general.news')}</h2>
			<div className="post-feed">
				{data.posts.edges.map(
					(post: { node: Index_posts_edges_node }, i: number) => {
						return <PostCard post={post.node} i={i} key={i} isHome />;
					},
				)}
			</div>
		</div>
	);
};
