import * as React from 'react';
//import { courses } from './data';
//import { Twemoji } from 'react-emoji-render';
import Img from 'gatsby-image';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import styles from './Services.module.css';
import { graphql, useStaticQuery } from 'gatsby';
import Link from '~components/utils/Link/Link';

const Services = () => {
	const data = useStaticQuery(QUERY);
	const { formatMessage } = useIntlHelper();
	return (
		<div className="inner">
			{/* 			<header className={styles.productsPageHeader}>
			>
			</header> */}
			<div className={styles.feed}>
				{data.services.sections.map(section => (
					<article key={section.id} className={styles.card} id={section.id}>
						<Img
							className={styles.cardImg}
							fluid={section.image.childImageSharp.fluid}
						/>

						<div className={styles.cardContent}>
							<div className={styles.cardContentLink}>
								<header className={styles.cardHeader}>
									<h2 className={styles.cardTitle}>
										{formatMessage(section.id + '.title')}
									</h2>
								</header>
								<section className={styles.cardExcerpt}>
									{formatMessage(section.id + '.desc')}
									{section.links.map(link => (
										<p className={styles.cardLink} key={link.id}>
											<Link to={link.link}>{formatMessage(link.id)} </Link>
										</p>
									))}
								</section>
							</div>
						</div>
					</article>
				))}
			</div>
		</div>
	);
};

export default Services;

const QUERY = graphql`
	query ServicesQuery {
		services: productsYaml(id: { eq: "services" }) {
			id
			sections {
				id
				image {
					childImageSharp {
						fluid(maxWidth: 500) {
							...GatsbyImageSharpFluid_withWebp_tracedSVG
						}
					}
				}
				links {
					id
					link
				}
			}
		}
	}
`;
