import * as React from 'react';
import { graphql } from 'gatsby';
import { Index } from './__generated__/Index';
import Layout from '~components/Layout/Template/Layout';
import { PostFeed } from '~components/Sections/PostFeed/PostFeed';
import CarouselCards from '~components/Sections/CardsCarousel/Carousel';
import Products from '~components/Sections/Products/Products';
import useIntlHelper from '~components/utils/Intl/IntlHelper';
import Services from '~components/Sections/Services/Services';
import Applications from '~components/Sections/Applications/Applications';
//import { IndexHero } from '~components/Layout/Headers/Hero';

const IndexPage: React.FC<{ data: Index }> = ({ data }) => {
	const stickyRef: React.RefObject<HTMLDivElement> = React.useRef(null);

	const { formatMessage } = useIntlHelper();
	return (
		<>
			<Layout template="home" stickyRef={stickyRef}>
				{/* 	<IndexHero /> */}
				<div className="inner" ref={stickyRef}>
					<h2>{formatMessage('general.carouselHeader')}</h2>
					<CarouselCards />
					<h2> {formatMessage('products.title')}</h2>
					<Products />
					<h2> {formatMessage('applications.title')}</h2>
					<Applications />
					<h2> {formatMessage('services.title')}</h2>
					<Services />
					<PostFeed data={data} />

					{/* 	<Offcanvas /> */}
				</div>
			</Layout>
		</>
	);
};

export default IndexPage;
export const indexQuery = graphql`
	query Index($locale: String!) {
		posts: allMdx(
			sort: { fields: [frontmatter___date], order: DESC }
			limit: 20
			filter: {
				fields: { locale: { eq: $locale } }
				frontmatter: { tags: { in: "news" } }
			}
		) {
			edges {
				node {
					fields {
						slug
					}
					frontmatter {
						date(formatString: "DD MMMM YYYY")
						title
						featured
						category
						tags
						image {
							childImageSharp {
								fluid {
									...GatsbyImageSharpFluid_withWebp_tracedSVG
								}
							}
						}
						authors {
							id
							avatar {
								childImageSharp {
									fluid(maxWidth: 250, maxHeight: 250) {
										...GatsbyImageSharpFluid_withWebp_tracedSVG
									}
								}
							}
						}
					}

					excerpt(truncate: true, pruneLength: 200)
					timeToRead
				}
			}
		}
		indexCover: file(
			relativePath: { eq: "assets/images/publication-cover.png" }
		) {
			childImageSharp {
				fluid(quality: 100) {
					...GatsbyImageSharpFluid_withWebp
					...GatsbyImageSharpFluidLimitPresentationSize
				}
			}
		}
	}
`;
